.slide-container {
  position: relative
}

.slide-scroller {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0)
}

.slide-scroller.animation {
  -webkit-transition: .5s -webkit-transform ease;
  -moz-transition: .5s -moz-transform ease;
  -ms-transition: .5s -ms-transform ease;
  transition: .5s transform ease
}

.scroller {
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch
}

.scroller-disabled {
  display: none
}

.scroller-hidden {
  overflow-y: hidden
}

.cam .custom-scrollbar,
.cam-single-pane .custom-scrollbar {
  transition: background-color .35s
}

.cam .custom-scrollbar::-webkit-scrollbar,
.cam-single-pane .custom-scrollbar::-webkit-scrollbar {
  width: 10px
}

.cam .custom-scrollbar::-webkit-scrollbar-track,
.cam-single-pane .custom-scrollbar::-webkit-scrollbar-track {
  background-color: #fff
}

.cam .custom-scrollbar::-webkit-scrollbar-track :disabled,
.cam-single-pane .custom-scrollbar::-webkit-scrollbar-track :disabled {
  display: none
}

.cam .custom-scrollbar::-webkit-scrollbar-thumb,
.cam-single-pane .custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: inherit;
  border: 2px solid #fff
}

.custom-scrollbar .flex-content {
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  -webkit-flex: 1 0 auto;
  flex: 1 0 auto
}

.cam .custom-scrollbar.theme-cam-menu::-webkit-scrollbar-thumb,
.cam-single-pane .custom-scrollbar.theme-cam-menu::-webkit-scrollbar-thumb {
  border: 2px solid
}

.is-page-loading,
.list-puller-loading,
.loading-animation {
  -webkit-animation: spin 1.2s linear infinite;
  -moz-animation: spin 1.2s linear infinite;
  animation: spin 1.2s linear infinite
}

.is-page-loading {
  background-image: url("./assets/loading.png");
  width: 5rem;
  height: 5rem
}

.is-page-loading.white {
  background-image: url("./assets/loading_white_l.png")
}

.loading-base {
  background-image: url("./assets/loading_base.png");
  width: 8.6rem;
  height: 8.6rem
}

.list-puller-loading {
  background-image: url("./assets/loading_blue_m.png");
  width: 3.4rem;
  height: 3.4rem
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg)
  }
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg)
  }
}

@keyframes spin {
  100% {
    transform: rotate(360deg)
